@font-face {
  font-family: 'NocturnoDisplayProMedium';
  src: url('../fonts/NocturnoDisplayProMedium.otf') format("opentype");
}

@font-face {
  font-family: 'Larsseit-Light';
  src: url('../fonts/Larsseit-Light.otf') format("opentype");
}

@font-face {
  font-family: 'Larsseit-Medium';
  src: url('../fonts/Larsseit-Medium.otf') format("opentype");
}

.heading-1-wrapper {
  display: block;
}

.center-text {
  text-align: center;
}

.heading-1 {
  font-family: 'NocturnoDisplayProMedium';
  display: inline-block;
  font-size: 60px;
  line-height: 60px;
}
.heading-1:after {
  content: '';
  display: block;
  border-top: 1px solid #e3e9ed;
  margin: .5em 0 .5em;
}

.heading-3 {
  font-family: 'Larsseit-Medium';
  font-size: 25px;
  line-height: 25px;
}

.heading-4 {
  font-family: 'NocturnoDisplayProMedium';
  font-size: 30px;
  line-height: 20px;
}

.profile-name {
  font-family: 'Larsseit-Medium';
  font-size: 20px;
  line-height: 20px;
  padding-top: 15px;
}

.profile-copy {
  font-family: 'Larsseit-Light';
  font-size: 14px;
}

.section-copy {
  font-family: 'Larsseit-Light';
  font-size: 18px;
  line-height: 20px;
}

.section-sub-copy {
  font-family: 'Larsseit-Light';
  font-size: 14px;
  line-height: 14px;
}

.submit-button {
  font-family: 'Larsseit-Light';
  font-size: 12px;
  border-radius: 3px;
  color: #000000;
}
.social-icon {
  font-size: 16px;
  padding-left: 6px;
  padding-right: 6px;
}

a {
  font-family: 'Larsseit-Medium';
  font-size: 14px;
  color: #ffffff;
}

a.link:hover {
  font-family: 'Larsseit-Medium';
  font-size: 14px;
  color: #ffffff;
}

